/* Modal Styles - Custom Modal Structure */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.modal-overlay.open {
  opacity: 1;
  visibility: visible;
}

/* Modal Container */
.modal-container {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow: hidden;
  transform: scale(0.9) translateY(20px);
  transition: all 0.3s ease;
  position: relative;
}

.modal-overlay.open .modal-container {
  transform: scale(1) translateY(0);
}

/* Modal Header */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #e5e7eb;
  background: #26667F;
  color: white;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  color: white;
  order: 1;
}

.close-btn {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  order: 2;
}

.close-btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
}

/* Modal Body */
.modal-body {
  padding: 2rem;
  max-height: 70vh;
  overflow-y: auto;
}

/* Form Sections */
.form-section {
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.section-title {
  color: #495057;
  font-weight: 600;
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #dee2e6;
}

/* Form Controls */
.form-label {
  font-weight: 500;
  color: #495057;
  margin-bottom: 0.5rem;
}

.form-control,
.form-select {
  border: 1px solid #dee2e6;
  border-radius: 6px;
  padding: 0.75rem;
  font-size: 0.875rem;
  transition: all 0.2s ease;
}

.form-control:focus,
.form-select:focus {
  border-color: #2c3e50;
  box-shadow: 0 0 0 0.2rem rgba(44, 62, 80, 0.25);
}

.form-control.is-invalid,
.form-select.is-invalid {
  border-color: #dc3545;
  box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

.invalid-feedback {
  font-size: 0.75rem;
  color: #dc3545;
  margin-top: 0.25rem;
}

/* Form Grid */
.row {
  margin-left: -0.75rem;
  margin-right: -0.75rem;
}

.col-md-6 {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
}

/* Buttons */
.btn {
  border-radius: 6px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.btn-primary {
  background: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
  border: none;
}

.btn-primary:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
}

.btn-outline-primary {
  border-color: #2c3e50;
  color: #2c3e50;
}

.btn-outline-primary:hover {
  background: #2c3e50;
  border-color: #2c3e50;
  color: white;
}

/* Modal Footer */
.modal-footer {
  background: #f8f9fa;
  border-top: 1px solid #e9ecef;
  padding: 1.5rem;
  border-radius: 0 0 12px 12px;
}

.modal-footer .btn {
  padding: 0.75rem 1.5rem;
  font-weight: 500;
}

/* Loading States */
.spinner-border-sm {
  width: 1rem;
  height: 1rem;
}

/* Error Display */
.alert {
  border-radius: 8px;
  border: none;
  padding: 1rem;
  margin-bottom: 1.5rem;
}

.alert-danger {
  background: #f8d7da;
  color: #721c24;
  border-left: 4px solid #dc3545;
}

.alert-info {
  background: #d1ecf1;
  color: #0c5460;
  border-left: 4px solid #17a2b8;
}

.alert svg {
  color: inherit;
}

/* Responsive Design */
@media (max-width: 768px) {
  .modal-lg {
    max-width: 95%;
    margin: 1rem;
  }
  
  .modal-body {
    padding: 1rem;
    max-height: 60vh;
  }
  
  .form-section {
    padding: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .row {
    margin-left: -0.5rem;
    margin-right: -0.5rem;
  }
  
  .col-md-6 {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  
  .modal-footer {
    padding: 1rem;
  }
}

@media (max-width: 576px) {
  .modal-lg {
    max-width: 100%;
    margin: 0;
    height: 100vh;
  }
  
  .modal-dialog {
    height: 100vh;
    margin: 0;
  }
  
  .modal-content {
    height: 100vh;
    border-radius: 0;
  }
  
  .modal-header {
    border-radius: 0;
  }
  
  .modal-footer {
    border-radius: 0;
  }
  
  .form-section {
    margin-bottom: 1rem;
  }
}

/* RTL Support */
[dir="rtl"] .modal-header {
  text-align: right;
  flex-direction: row-reverse;
}

[dir="rtl"] .modal-header .modal-title {
  order: 2;
}

[dir="rtl"] .modal-header .close-btn {
  order: 1;
}

[dir="rtl"] .modal-title {
  font-family: 'IBM Plex Sans Arabic', sans-serif !important;
}

[dir="ltr"] .modal-title {
  font-family: 'Poppins', sans-serif !important;
}

[dir="rtl"] .modal-footer {
  flex-direction: row-reverse;
}

/* Accessibility */
.form-control:focus,
.form-select:focus,
.btn:focus {
  outline: 2px solid #043DFF;
  outline-offset: 2px;
}

/* Custom Scrollbar */
.modal-body::-webkit-scrollbar {
  width: 6px;
}

.modal-body::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

.modal-body::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

/* RTL Dropdown Arrow Alignment */
[dir="rtl"] .form-select {
  background-position: left 0.75rem center;
  padding-left: 2.25rem;
  padding-right: 0.75rem;
}

[dir="ltr"] .form-select {
  background-position: right 0.75rem center;
  padding-right: 2.25rem;
  padding-left: 0.75rem;
}

/* Ensure dropdown arrows don't overlap with text */
[dir="rtl"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

[dir="ltr"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

/* Focus state for RTL dropdowns */
[dir="rtl"] .form-select:focus {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select:focus {
  background-position: right 0.75rem center;
}

/* Invalid state for RTL dropdowns */
[dir="rtl"] .form-select.is-invalid {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select.is-invalid {
  background-position: right 0.75rem center;
}

/* RTL Checkbox Support */
.form-check.rtl {
  direction: rtl;
  text-align: right;
}

.form-check.rtl .form-check-input {
  margin-left: 0.5rem;
  margin-right: 0;
  float: right;
}

.form-check.rtl .form-check-label {
  margin-right: 0.5rem;
  margin-left: 0;
  font-family: 'IBM Plex Sans Arabic', sans-serif;
}

/* LTR Checkbox Support */
.form-check:not(.rtl) {
  direction: ltr;
  text-align: left;
}

.form-check:not(.rtl) .form-check-input {
  margin-right: 0.5rem;
  margin-left: 0;
  float: left;
}

.form-check:not(.rtl) .form-check-label {
  margin-left: 0.5rem;
  margin-right: 0;
  font-family: 'Poppins', sans-serif;
}